//主页
import React from 'react';
import {Row,Col} from 'react-bootstrap';
import Slider from '../../components/slider/Slider';
import Mlist from '../../components/list/Mlist';
import Card from '../../components/card/Card';
import Panel from '../../components/panel/Panel';
import Tabs from '../../components/tabs/Tabs';


const IMAGE_DATA_1 = [
  {
    src: require('./images/1.jpg'),
    alt: 'images-1',
  },
  {
    src: require('./images/2.jpg'),
    alt: 'images-2',
  }
];
const IMAGE_DATA_2 = [
  {
    src: require('./images/东山湾生态浮标.jpg'),
    alt: '东山湾生态浮标',
  },
  {
    src: require('./images/厦门中心站完成福建.jpg'),
    alt: '厦门中心站完成福建',
  },
  {
    src: require('./images/厦门中心站.jpg'),
    alt: '厦门中心站',
  }
];
const LIST_DATA=[
  {
    content:'厦门中心站完成福建南部海域八月综合“体检”任务',
    tag:'2016-10-24'
  },
  {
    content:'东山湾生态浮标完成大修并重新布放',
    tag:'2016-08-10'
  },
  {
    content:'莆田海洋站站部正式动工',
    tag:'2016-08-04'
  },
  {
    content:'厦门中心站第三党支部开展“两学一做”专题党课教育',
    tag:'2016-08-04'
  },
  {
    content:'厦门中心站创新培训方式凝聚团队精神',
    tag:'2016-07-04'
  },
  {
    content:'“苏迪罗”强台风登陆福建，厦门中心站精心部署全力应对',
    tag:'2016-07-04'
  }
];

class home extends React.Component{
  constructor(props) {
      super(props);
  }
  render() {
      return  <div>
                <div style={{height:"220px"}}>
                  <Col md={6}>
                         <Slider
                          items={IMAGE_DATA_2}
                          speed={1.2}
                          delay={2.1}
                          pause={true}
                          autoplay={true}
                          dots={true}
                          arrows={false}
                          istext={true}
                          height={"300px"}
                        />
                  </Col>
                  <Col md={6} style={{marginTop:"30px"}}>
                    <Card title={'工作动态'} card_content={{height:"250px"}}>
                      <Mlist list={LIST_DATA} />
                    </Card>
                  </Col>
                </div>
                <Col md={12} style={{marginTop:"20px"}}>
                  <Panel panel_body={{height:"400px"}}>
                    <Tabs>
                      <div name="测试1">test1</div>
                      <div name="测试2">test2</div>
                      <div name="测试3">test3</div>
                    </Tabs>
                  </Panel>
                </Col>
                <Col md={12}>
                  <div style={{height:"170px"}}>
                    <Slider
                        items={IMAGE_DATA_1}
                        speed={1.2}
                        delay={2.1}
                        pause={true}
                        autoplay={true}
                        dots={true}
                        arrows={false}
                        height={"170px"}
                      />
                  </div>
                </Col>
              </div>

      }
};
export default home;



